@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --clr-dark-1: #1d3557;
  --clr-dark-2: #457b9d;
  --clr-light-1: #a8dadc;
  --clr-light-2: #f1faee;
  --clr-accent: #e63946;

  --bs-deep: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  --bs-subtle: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
  --bs-hover: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 250ms ease-out;
}

body {
  font-family: 'Roboto'; 
  background-color: #1d3557; 
  background-color: var(--clr-dark-1);
} 

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins';
  font-weight: 500;
}

path[fill='#123456']{display:none !important;}
path{
    cursor:pointer;
    stroke:rgba(100, 100, 100, 0.301);
}

.chart-container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden !important;
    justify-content:center;
    align-items: center;
    overflow: scroll;
    background: var(--clr-light-2);
    padding: 2em 0;
    border-bottom-left-radius: 38px;
    border-bottom-right-radius: 38px;
    box-shadow: var(--bs-subtle);
}

.chart-heading {
    width: 100vw;
    text-align: center;
    color: var(--clr-dark-1);
}

.table-container {
    background-color: #fff;
}

.cards-container {
    width: 100%;
    display: flex;
    padding: 2em;
    background-color: var(--clr-dark-1);
}

.text-info {
    font-size: 1rem;
    margin-top: 2em;
    width: 100vw;
    text-align: center;
    color: var(--clr-dark-1);
    font-weight: 700;
    font-family: "Poppins";
}

.text-sorry {
    width: 10vw;
    font-size: 0.8rem;
    color: var(--clr-dark-1);
}

.text-sorry i {
    color: var(--clr-accent);
}

@media(max-width: 600px) {
    .cards-container{
        flex-direction: column;
    }

    .text-sorry {
        width: 90vw;
        margin: 5px auto;
    }
}

.card {
    width: 25%;
    min-height: 90px;
    border-radius: 1em;
    box-shadow: var(--bs-subtle);
    cursor: pointer;

    display: flex;
    align-items: center;
    padding: 0 1em;
}

.card-info {
    padding: 1em;
    height: 100%;
    text-align: center;
}

.card-info p {
    font-family: 'Poppins';
    font-size: 3em;
    font-weight: 900;
}

.card + .card {
    margin-left: 1em;
}

.card-blue {
    background-color: #abe9cd;
background-image: linear-gradient(315deg, #abe9cd 0%, #3eadcf 74%);

}

.card-red {
    background-color: #fc9842;
    background-image: linear-gradient(315deg, #fc9842 0%, #fe5f75 74%);
}

.card-green {
    background-color: #00b712;
    background-image: linear-gradient(315deg, #00b712 0%, #5aff15 74%);
}

.card-grey {
    background-color: #f8f9d2;
    background-image: linear-gradient(315deg, #f8f9d2 0%, #e8dbfc 74%);
}

@media(max-width: 600px) {
    .card{
        width: 100%;
        margin: 10px 0 !important;
    }
}

.news {
    background: blue;
}
